export default class SoundService {
  constructor() {
    let AudioContext = window.AudioContext || window.webkitAudioContext || window.mozAudioContext;
    this.context = new AudioContext();

    // ios workaround
    window.addEventListener('touchstart', () => {
      // TODO can be improved
      const buffer = this.context.createBuffer(1, 1, 22050);
      const source = this.context.createBufferSource();
      source.buffer = buffer;

      // connect to output (your speakers)
      source.connect(this.context.destination);

      // play the file
      source.start();
      source.stop();
    });
  }

  play() {
    this.source = this.context.createBufferSource();
    this.context.decodeAudioData(decodeSound(), (buffer) => {
      this.source.buffer = buffer;
      this.source.connect(this.context.destination);
      this.source.loop = true;
      this.source.start(0);
    });
  }

  stop() {
    if (this.source) {
      this.source.stop();
    }
  }
}

function decodeSound() {
  let byteCharacters = atob('');
  let byteNumbers = byteCharacters.split('').map(char => char.charCodeAt(0));
  return new Uint8Array(byteNumbers).buffer;
}



